p{
	text-align: center;	
	font-size: 1.33em;
}

.app{	
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
 	grid-template-rows: 1fr 1fr 1fr 1fr;
 
 	grid-template-areas: "head head head"
                         "navi navi navi"
                         "lMai main main"
                         "foot foot foot";
 
 	grid-gap: 0;

	/* this is needed?  */
	height: 100vh;
	width: 100vw;
}

.header
{
	grid-area: "head";

	grid-column-start: 1;
    grid-column-end: 4;

	grid-row-start: 1;
	grid-row-end: 2;

	height: 15vh;

	background-color: beige;
}

.navigation-menu
{
	grid-area: "navi";

	grid-column-start: 1;
	grid-column-end: 4;

	grid-row-start: 2;
	grid-row-end: 3;

	height: 10vh;
	background-color: #e9e9e9;

	overflow: hidden;
}

/* Style the links inside the navigation bar */
.navigation-menu a {
  float: left;
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}



/* Change the color of links on hover */
.navigation-menu a:hover {
  background-color: #ddd;
  color: black;
}

/* Style the "active" element to highlight the current page */
.navigation-menu a.active {
  background-color: #2196F3;
  color: white;
}

/* Style the search box inside the navigation bar */
.navigation-menu input[type=text] {
  float: right;
  padding: 6px;
  border: none;
  margin-top: 8px;
  margin-right: 16px;
  font-size: 17px;
}


.main-content
{
	grid-area: "main";

	grid-column-start: 2;
	grid-column-end: 4;

	grid-row-start: 3;
	grid-row-end: 4;

	height: 60vh;
	background-color: yellow;

	overflow: hidden;	
}




.left-main-content
{
	grid-area: "lMai";

	grid-column-start: 1;
	grid-column-end: 2;

	grid-row-start: 3;
	grid-row-end: 4;

	height: 60vh;
	background-color: teal;
}

.footer
{
	grid-area: "foot";

	grid-column-start: 1;
	grid-column-end: 4;

	grid-row-start: 4;
	grid-row-end: 5;

	height: 15vh;
	background-color: orange;
}
